  transition: all 0.3s ease;
}

.review-btn:hover,
.reorder-btn:hover {
  background-color: #007bff;
  color: white;
}

/* åˆ†é æ¨£å¼ */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 30px;
  padding: 20px;
}

.page-btn {
  padding: 8px 16px;
  border: 1px solid #64a7ee;
  background-color: white;
  color: #549be8;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.3s ease;
}

.page-btn:hover:not(:disabled) {
  background-color: #4d97e7;
  color: white;
}

.page-btn:disabled {
  border-color: #ddd;
  color: #999;
  cursor: not-allowed;
  opacity: 0.5;
}

.page-info {
  font-size: 14px;
  color: #666;
  font-weight: 500;
}
</style>