<template>
<div class="shopping-cart">
  <!-- åŠŸèƒ½åˆ—è¡¨ -->
  <div class="cart-header">
    <div class="header-row">
      <div class="col-select">
        <input type="checkbox">
      </div>
      <div class="col-product">å•†å“</div>
      <div class="col-price">å–®åƒ¹(1æ–¤)</div>
      <div class="col-quantity">æ•¸é‡</div>
      <div class="col-total">ç¸½è¨ˆ</div>
      <div class="col-action">æ“ä½œ</div>
    </div>
    </div>
  
  <!-- å•†å“åˆ—è¡¨ -->
  <div class="cart-body">
    <ul class="cart-list">
      <li v-for="product in products" :key="product.id" class="cart-products">
        <div class="col-select"> 
          <input type="checkbox" v-model="product.selected">
        </div>
        <div class="col-product">
          <img :src="product.image_url" :alt="product.name" class="product-img">
          <span class="product-name">{{ product.name }}</span>
        </div>
        <div class="col-price">
          <span class="price">{{ product.price }}</span>
        </div>
        <div class="col-quantity">
          <button @click="decreaseQuantity(product)">-</button>
          <input type="number" v-model="product.cartQuantity">
          <button @click="increaseQuantity(product)">+</button>
        </div>
        <div class="col-total">
          <span class="total-price">${{ totalPrice(product) }}</span>
        </div>
        <div class="col-delete">
          <button class="remove-btn" @click="removeProduct(product)">åˆªé™¤</button>
        </div>
      </li>
    </ul>
  </div>

  <!-- åº•éƒ¨ï¼šçµå¸³å€åŸŸ -->
  <div class="cart-footer">
    <button class="checked-btn" @click="OrderProducts">çµå¸³</button>
  </div>
</div>
</template>


<script>
export default {
  data() {
    return {
      products: [],     // é¡¯ç¤º-å•†å“
      cartItems: [],    // å„²å­˜-è³¼ç‰©è»Šå•†å“
      memberId: null    // æœƒå“¡ ID
    }
  },
  
  // çµ„ä»¶è¼‰å…¥æ™‚åŸ·è¡Œ
  async mounted() {
    // 1. å…ˆå–å¾—æœƒå“¡ ID
    await this.getMemberId();
    
    // å¦‚æœæ²’æœ‰æœƒå“¡ IDï¼Œç›´æ¥è¿”å›ï¼ˆæ””æˆªå™¨æœƒè™•ç†ç™»å…¥å¤±æ•ˆï¼‰
    if (!this.memberId) {
      return;
    }
    
    // 2. å¾ localStorage è®€å–è©²æœƒå“¡çš„è³¼ç‰©è»Š
    const cartKey = `cart_${this.memberId}`;
    this.cartItems = JSON.parse(localStorage.getItem(cartKey)) || [];
    console.log(`æœƒå“¡ ${this.memberId} çš„è³¼ç‰©è»Šè³‡æ–™:`, this.cartItems);
    
    if (this.cartItems.length > 0) {
      console.log('è¼‰å…¥è³¼ç‰©è»Šå•†å“...');
      await this.loadCartProducts();
    } else {
      console.log('è³¼ç‰©è»Šç‚ºç©º');
      alert('è³¼ç‰©è»Šæ˜¯ç©ºçš„ï¼Œè«‹å…ˆåˆ°å•†åº—é é¢åŠ å…¥å•†å“ï¼');
    }
  },
  
  methods: {
    // å–å¾—æœƒå“¡ ID
    async getMemberId() {
      try {
        const response = await this.axios.get('http://localhost:3000/member/verify', {
          withCredentials: true
        });
        
        if (response.data.result && response.data.result.status === 'valid') {
          this.memberId = response.data.result.memberId;
          console.log('æœƒå“¡ ID:', this.memberId);
        }
      } catch (error) {
        console.error('å–å¾—æœƒå“¡ ID å¤±æ•—:', error);
      }
    },
    
    async loadCartProducts() {
      try {
        console.log('è¼‰å…¥è³¼ç‰©è»Šå•†å“:', this.cartItems);
        
        // å…ˆç²å–æ‰€æœ‰å•†å“è³‡æ–™
        const response = await this.axios.get('http://localhost:3000/api/products', {
          withCredentials: true
        });
        
        const allProducts = response.data.products || [];
        console.log('æ‰€æœ‰å•†å“è³‡æ–™:', allProducts);
        
        // æ¸…ç©ºç¾æœ‰å•†å“åˆ—è¡¨
        this.products = [];
        
        // æ ¹æ“šè³¼ç‰©è»ŠIDéæ¿¾å•†å“ä¸¦çµåˆæ•¸é‡
        for (let cartItem of this.cartItems) {
          const product = allProducts.find(p => p.id === cartItem.productId);
          
          if (product) {
            // çµåˆå•†å“è³‡æ–™å’Œè³¼ç‰©è»Šæ•¸é‡
            const cartProduct = {
              ...product,
              cartQuantity: cartItem.quantity || 1,  // è³¼ç‰©è»Šä¸­çš„æ•¸é‡
              selected: false  // é è¨­æœªé¸ä¸­
            };
            
            this.products.push(cartProduct);
            console.log('è¼‰å…¥å•†å“:', cartProduct.name, 'æ•¸é‡:', cartProduct.cartQuantity);
          } else {
            console.warn(`æ‰¾ä¸åˆ°å•†å“ ID: ${cartItem.productId}`);
          }
        }
        
        console.log('æœ€çµ‚å•†å“åˆ—è¡¨:', this.products);
        
      } catch (error) {
        console.error('è¼‰å…¥è³¼ç‰©è»Šå•†å“å¤±æ•—:', error);
        alert('è¼‰å…¥è³¼ç‰©è»Šå•†å“å¤±æ•—ï¼š' + error.message);
      }
    },
    
    // å¢åŠ æ•¸é‡
    increaseQuantity(product) {
      product.cartQuantity++;
      this.updateLocalStorage();
    },
    
    // æ¸›å°‘æ•¸é‡
    decreaseQuantity(product) {
      if (product.cartQuantity > 1) {
        product.cartQuantity--;
        this.updateLocalStorage();
      }
    },
    
    // è¨ˆç®—ç¸½åƒ¹
    totalPrice(product) {
      return (product.cartQuantity || 1) * (product.price || 0);
    },
    
    // æ›´æ–° localStorage
    updateLocalStorage() {
      const updatedCart = this.products.map(product => ({
        productId: product.id,
        quantity: product.cartQuantity
      }));
      const cartKey = `cart_${this.memberId}`;
      localStorage.setItem(cartKey, JSON.stringify(updatedCart));
      console.log(`æœƒå“¡ ${this.memberId} è³¼ç‰©è»Šå·²æ›´æ–°:`, updatedCart);
    },
    
    // åˆªé™¤å•†å“
    removeProduct(product) {
      if (confirm(`ç¢ºå®šè¦å¾è³¼ç‰©è»Šä¸­ç§»é™¤ã€Œ${product.name}ã€å—ï¼Ÿ`)) {
        // å¾å•†å“åˆ—è¡¨ä¸­ç§»é™¤
        const productIndex = this.products.findIndex(p => p.id === product.id);
        if (productIndex > -1) {
          this.products.splice(productIndex, 1);
        }
        
        // å¾è³¼ç‰©è»Šè³‡æ–™ä¸­ç§»é™¤
        const cartIndex = this.cartItems.findIndex(item => item.productId === product.id);
        if (cartIndex > -1) {
          this.cartItems.splice(cartIndex, 1);
        }
        
        // æ›´æ–° localStorageï¼ˆç¶å®šæœƒå“¡ IDï¼‰
        const cartKey = `cart_${this.memberId}`;
        localStorage.setItem(cartKey, JSON.stringify(this.cartItems));
        
        console.log(`å•†å“ ${product.name} å·²å¾è³¼ç‰©è»Šç§»é™¤`);
        console.log('æ›´æ–°å¾Œçš„è³¼ç‰©è»Š:', this.cartItems);
        
        // å¦‚æœè³¼ç‰©è»Šç©ºäº†ï¼Œé¡¯ç¤ºæç¤º
        if (this.products.length === 0) {
          alert('è³¼ç‰©è»Šå·²æ¸…ç©ºï¼');
        }
      }
    },
    async OrderProducts() {
      try {
        // æª¢æŸ¥æ˜¯å¦æœ‰å•†å“
        if (this.products.length === 0) {
          alert('è³¼ç‰©è»Šæ˜¯ç©ºçš„ï¼');
          return;
        }

        const response = await this.axios.post('http://localhost:3000/api/orders', {
          productID: this.products.map(p => p.id).join(','),        
          quantity: this.products.map(p => p.cartQuantity).join(',') 
        }, {
          withCredentials: true
        });

        // è™•ç†å›æ‡‰
        if (response.data.result.status === "è¨‚å–®å·²æˆåŠŸå»ºç«‹ã€‚") {
          alert('è¨‚å–®å»ºç«‹æˆåŠŸï¼');
          
          // æ¸…ç©ºè³¼ç‰©è»Š
          this.products = [];
          this.cartItems = [];
          localStorage.removeItem(`cart_${this.memberId}`);
          
          console.log('è³¼ç‰©è»Šå·²æ¸…ç©º');
        } else {
          alert('è¨‚å–®å»ºç«‹å¤±æ•—ï¼š' + (response.data.result.err || 'æœªçŸ¥éŒ¯èª¤'));
        }
        
      } catch (error) {
        console.error('å»ºç«‹è¨‚å–®å¤±æ•—:', error);
        alert('å»ºç«‹è¨‚å–®å¤±æ•—ï¼š' + (error.response?.data?.result?.err || error.message));
      }
    }

  }
}
</script>

<style>
.header-row {
  display: grid;
  grid-template-columns: 60px 1fr 120px 150px 120px 100px;
  gap: 15px;
  align-items: center;
  padding: 20px;
  margin: 20px auto 0 auto;  /* è‡ªå‹•ç½®ä¸­ */
  max-width: 950px;         /* å›ºå®šæœ€å¤§å¯¬åº¦ */
  border: 1px solid #e2dddd;
}

.col-select { text-align: center; }
.col-product, .col-price, .col-quantity, .col-total, .col-action, .col-delete { text-align: center; }
.col-quantity input {
  width: 50px;
  text-align: center; 
}
/* é‡ç½® ul çš„é è¨­æ¨£å¼ */
.cart-list {
  list-style: none;  /* ç§»é™¤åˆ—è¡¨ç¬¦è™Ÿ */
  padding: 0;        /* ç§»é™¤é è¨­ padding */
  margin: 0;         /* ç§»é™¤é è¨­ margin */
}

.cart-products {
  display: grid;
  grid-template-columns: 60px 1fr 120px 150px 120px 100px;
  gap: 15px;
  align-items: center;
  padding: 10px 20px;           /* èˆ‡ header-row ç›¸åŒçš„å·¦å³ padding */
  margin: 15px auto;            /* è‡ªå‹•ç½®ä¸­å°é½Š */
  max-width: 950px;             /* èˆ‡ header-row ç›¸åŒçš„æœ€å¤§å¯¬åº¦ */
  border: 1px solid #5f4444;
}


.product-img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 4px;
  margin-bottom: 10px;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.cart-footer {
  text-align: right;
