  .cart-products {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    padding: 12px;
    margin: 10px;
    max-width: 100%;
  }

  /* åœ–ç‰‡+åç¨±å€åŸŸ */
  .col-product {
    display: flex;
    align-items: center;
    flex: 1;
    min-width: 120px;
  }

  .product-img {
    width: 50px;
    height: 50px;
    margin-right: 8px;
    margin-bottom: 0;
  }
  
  .product-name {
    font-size: 14px;
  }

  /* æ•¸é‡å€åŸŸ */
  .col-quantity {
    display: flex;
    flex: 1;
    align-items: center;
    gap: 12px;
  }
  
  .col-quantity button {
    width: 28px;
    height: 28px;
    padding: 0;
    font-size: 16px;
  }
  
  .col-quantity input {
    width: 40px;
    height: 28px;
    text-align: center;
    font-size: 14px;
  }

  /* ç¸½è¨ˆ */
  .col-total {
    font-weight: bold;
    color: #c83a3a;
    font-size: 16px;
    flex: 0.3;
  }

  /* åˆªé™¤æŒ‰éˆ• */
  .col-delete {
    margin-left: auto;
    font-size: 9px;
  }
  
  .remove-btn {
    padding: 6px 12px;
    font-size: 13px;
  }
}

</style>