        </div>
    </div>
</template>

<script>
export default {
    name: 'ChangePasswordModal',
    data() {
        return {
            showModal: false,
            oldPassword: '',
            newPassword: '',
            loading: false
        }
    },
    methods: {
        closeModal() {
            this.showModal = false;
            this.oldPassword = '';
            this.newPassword = '';
        },
        OverlayClick(event) {
            // åªæœ‰ç•¶é»æ“Šçš„æ˜¯ overlay æœ¬èº«ï¼ˆä¸æ˜¯å­å…ƒç´ ï¼‰æ™‚æ‰é—œé–‰
            if (event.target === event.currentTarget) {
                this.closeModal();
            }
        },
        openModal() {
            this.showModal = true;
        },
        async handleChangePassword() {
            this.loading = true;
            try {
                const response = await this.axios.put('http://localhost:3000/member/update-password', {
                    oldPassword: this.oldPassword,
                    newPassword: this.newPassword
                }, {
                    withCredentials: true
                });
                
                if (response.data.result.status === 'å¯†ç¢¼æ›´æ–°æˆåŠŸã€‚') {
                    alert('å¯†ç¢¼æ›´æ–°æˆåŠŸï¼è«‹é‡æ–°ç™»å…¥ã€‚');
                    this.closeModal();
                    
                    // è§¸ç™¼æˆåŠŸäº‹ä»¶çµ¦çˆ¶çµ„ä»¶
                    this.$emit('changePassword-success', response.data.result);
                    
                    // å¯é¸ï¼šè‡ªå‹•ç™»å‡º
                    // this.$router.push('/login');
                } else {
                    alert(response.data.result.err || 'å¯†ç¢¼æ›´æ–°å¤±æ•—');
                }
                
            } catch (error) {
                console.error('è®Šæ›´å¯†ç¢¼å¤±æ•—:', error);
                alert('å¯†ç¢¼æ›´æ–°å¤±æ•—ï¼š' + (error.response?.data?.result?.err || 'ç¶²è·¯éŒ¯èª¤'));
            } finally {
                this.loading = false;
            }
        }
    }
}
</script>

